.done{
 background:
  linear-gradient(45deg, #4AE491, #49AA75);
 width: 300px;
 height: 60px;
 margin: 15px 0px;
 padding: 0px;
 color: #FAFAFA;
 display: flex;
 align-items: center;
 border-radius: 10px;
 text-transform:capitalize;

  animation: slideInDown 1s ease-in-out;
}
.done__title{
 margin-left: 15%;
 margin-right: auto;
}
.done__icons{
 margin: 5px;
 margin-left: 0px;
 display:flex;
 flex-direction: column;
 justify-content: flex-start;
}

@keyframes slideInDown {
  0% {
    transform: translateY(300px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
  }
}